import { observable } from 'mobx'

export interface IScrollerItemProps {
  id: number
  render?: () => JSX.Element
}
export declare type IScrollerItem = React.ReactElement<IScrollerItemProps>

export class Scroller {
  page: number = 1
  @observable items: any[] = []
  @observable hasMore: boolean = true

  /**
   *   item  item
   *   item 
   */
  heightMap = {}
  @observable startIndex: number = 0
  @observable endIndex: number = 0

  /**  observable */
  containerScrollTop: number = 0

  /**  Scroller  heightMap  */
  width: number
  /** resize  width  observable */
  @observable widthChanged: boolean = false

  addItems(items: IScrollerItem[], hasMore: boolean, page?: number) {
    // console.log('addItems')
    this.items.push(...items)
    this.hasMore = hasMore
    this.page = page ? page : this.page + 1
  }

  // 
  replaceItems(items: IScrollerItem[], hasMore: boolean, page?: number) {
    this.items = items
    this.hasMore = hasMore
    this.page = page || 1
  }

  updateIndex(startIndex: number, endIndex: number) {
    if (this.startIndex !== startIndex || this.endIndex !== endIndex) {
      // console.log('updateIndex', startIndex, endIndex)
      this.startIndex = startIndex
      this.endIndex = endIndex
    }
  }
}



